import React from 'react'
import './nextSectionArrow.css'

/**
 * Receive one Id and scroll to it
 * @param {goTo: String} props 
 * @returns 
 */
export default function NextArrowSection(props) {
    const goToElement = (id) => {
        const el = document.getElementById(id)
        var elDistanceToTop = window.pageYOffset + el.getBoundingClientRect().top
        
        window.scrollTo({
            top: elDistanceToTop,
            behavior: 'smooth',
        })
    }
    
    return (
        <>
        <div
            className="next-section-arrow-wrapper"
            onClick={() => goToElement(props.goTo)}
        >
            {
                props.text &&
                <span className="next-section-text">{props.text}</span>
            }
            <img
                className='down-arrow'
                src='./images/down-arrow.svg'
                alt='down-arrow'
            />
        </div>
        </>
    )
}